
"use client";

import { useState, useEffect, useMemo } from 'react';
import { db } from "@/lib/firebase";
import { collection, onSnapshot } from "firebase/firestore";
import { StudentProfile, Installment } from "@/lib/types";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Loader2, Wallet, FileText, CalendarClock } from "lucide-react";
import { format, isPast, isToday } from 'date-fns';

const getStatus = (installment: Installment): { text: string; variant: "default" | "secondary" | "destructive" | "outline" } => {
    if (installment.status === 'paid') {
        return { text: 'Paid', variant: 'default' };
    }
    const dueDate = new Date(installment.gracePeriodUntil || installment.dueDate);
    if (isPast(dueDate) && !isToday(dueDate)) {
        return { text: 'Overdue', variant: 'destructive' };
    }
    return { text: 'Unpaid', variant: 'secondary' };
};

export default function PaymentsPage() {
    const [students, setStudents] = useState<StudentProfile[]>([]);
    const [loading, setLoading] = useState(true);
    const [searchTerm, setSearchTerm] = useState("");

    useEffect(() => {
        setLoading(true);
        const unsub = onSnapshot(collection(db, 'students'), (snapshot) => {
            const allStudents = snapshot.docs
                .map(doc => ({ id: doc.id, ...doc.data() } as StudentProfile))
                .filter(student => student.paymentPlan && student.paymentPlan !== 'none');
            setStudents(allStudents);
            setLoading(false);
        }, (error) => {
            console.error("Error fetching students: ", error);
            setLoading(false);
        });
        return () => unsub();
    }, []);

    const filteredStudents = useMemo(() => {
        return students.filter(student =>
            student.name.toLowerCase().includes(searchTerm.toLowerCase())
        );
    }, [students, searchTerm]);

    const getOverallStatus = (student: StudentProfile): { text: string; variant: "default" | "destructive" | "secondary" } => {
        if (!student.installments || student.installments.length === 0) {
            return { text: "No Plan", variant: "secondary" };
        }
        const hasOverdue = student.installments.some(inst => getStatus(inst).text === 'Overdue');
        if (hasOverdue) {
            return { text: "Overdue", variant: "destructive" };
        }
        return { text: "Up to date", variant: "default" };
    };

    if (loading) {
        return (
            <div className="flex h-[80vh] items-center justify-center">
                <Loader2 className="h-8 w-8 animate-spin text-primary" />
            </div>
        );
    }

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h1 className="text-3xl font-bold font-headline flex items-center gap-2">
                    <Wallet className="w-8 h-8" />
                    Financial Management
                </h1>
            </div>
            <Card>
                <CardHeader>
                    <CardTitle>Student Payments</CardTitle>
                    <CardDescription>
                        View and manage student payment plans and installment statuses.
                    </CardDescription>
                    <div className="pt-4">
                        <Input
                            placeholder="Search by student name..."
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                            className="max-w-sm"
                        />
                    </div>
                </CardHeader>
                <CardContent>
                    <div className="border rounded-lg overflow-hidden">
                        <Accordion type="single" collapsible className="w-full">
                            {filteredStudents.length > 0 ? (
                                filteredStudents.map(student => (
                                    <AccordionItem value={student.id} key={student.id}>
                                        <AccordionTrigger className="px-4 py-2 hover:no-underline hover:bg-muted/50 w-full">
                                            <div className="grid grid-cols-4 w-full text-left">
                                                <span className="font-medium">{student.name}</span>
                                                <span>{student.id}</span>
                                                <span className="capitalize">{student.paymentPlan}</span>
                                                <Badge variant={getOverallStatus(student).variant} className="w-fit">
                                                    {getOverallStatus(student).text}
                                                </Badge>
                                            </div>
                                        </AccordionTrigger>
                                        <AccordionContent>
                                            <div className="bg-muted/50 p-4">
                                                <h4 className="font-semibold mb-2">Installment Details</h4>
                                                <Table>
                                                    <TableHeader>
                                                        <TableRow>
                                                            <TableHead>Due Date</TableHead>
                                                            <TableHead>Amount</TableHead>
                                                            <TableHead>Status</TableHead>
                                                            <TableHead className="text-right">Actions</TableHead>
                                                        </TableRow>
                                                    </TableHeader>
                                                    <TableBody>
                                                        {student.installments && student.installments.length > 0 ? (
                                                            student.installments.map(inst => (
                                                                <TableRow key={inst.id}>
                                                                    <TableCell>
                                                                        {format(new Date(inst.dueDate), "MMMM dd, yyyy")}
                                                                        {inst.gracePeriodUntil && (
                                                                            <p className="text-xs text-muted-foreground">
                                                                                Grace until {format(new Date(inst.gracePeriodUntil), "MMMM dd, yyyy")}
                                                                            </p>
                                                                        )}
                                                                    </TableCell>
                                                                    <TableCell>SAR {inst.amount.toFixed(2)}</TableCell>
                                                                    <TableCell>
                                                                        <Badge variant={getStatus(inst).variant}>
                                                                            {getStatus(inst).text}
                                                                        </Badge>
                                                                    </TableCell>
                                                                    <TableCell className="text-right">
                                                                        {getStatus(inst).text !== 'Paid' && (
                                                                            <div className="flex gap-2 justify-end">
                                                                                <Button variant="outline" size="sm">
                                                                                    <FileText className="mr-2 h-3 w-3" /> Record Payment
                                                                                </Button>
                                                                                <Button variant="ghost" size="sm">
                                                                                    <CalendarClock className="mr-2 h-3 w-3" /> Grace Period
                                                                                </Button>
                                                                            </div>
                                                                        )}
                                                                    </TableCell>
                                                                </TableRow>
                                                            ))
                                                        ) : (
                                                            <TableRow>
                                                                <TableCell colSpan={4} className="text-center">No installments found for this student.</TableCell>
                                                            </TableRow>
                                                        )}
                                                    </TableBody>
                                                </Table>
                                            </div>
                                        </AccordionContent>
                                    </AccordionItem>
                                ))
                            ) : (
                                <div className="p-4 text-center text-muted-foreground">
                                    No students match your search.
                                </div>
                            )}
                        </Accordion>
                    </div>
                </CardContent>
            </Card>
        </div>
    );
}
